/*
* 
*/
export const _trimLeft = (str) =>{
	str = str.replace(/^\s*/g,'');
	return str;
}

/*
* 
*/
export const _trimRight = (str) =>{
	str = str.replace(/(\s*$)/g,'');
	return str;
}

/*
* 
*/
export const _trim = (str) =>{
	str = str.replace(/\s*/g,"");
	return str;
}

/*
* 
*/
export const _trimAside = (str) =>{
	str = str.replace(/^\s*|\s*$/g,"");
	return str;
}
